<template>
  <loading v-if="!loaded"/>
  <div class="form-elements" v-else>
    <div class="row">
      <div class="col-md-12">
        <vuestic-widget :headerText="'menu.teams.update' | translate">
          <div class="row row-reverse">
            <div class="col-4">
              <div class="row row-reverse">
                <div class="col">
                  <div class="form-group with-icon-right"
                       :class="{'has-error': errors.has('name')}">
                    <div class="input-group">
                      <input
                        id="name"
                        name="name"
                        v-model="name"
                        v-validate="'required'"
                        required/>
                      <i
                        class="fa fa-exclamation-triangle icon-right input-icon"
                        v-show="errors.has('name')"></i>
                      <label class="control-label" for="name">{{'team.name'
                        | translate}}</label><i class="bar"></i>
                      <small v-show="errors.has('name')"
                             class="help text-danger">{{
                        errors.first('name')
                        }}
                      </small>
                    </div>
                  </div>
                </div>

                <div class="col">
                  <div class="form-group with-icon-right"
                       :class="{'has-error': errors.has('name_en')}">
                    <div class="input-group">
                      <input
                        id="name_en"
                        name="name_en"
                        v-model="name_en"
                        v-validate="'required'"
                        required/>
                      <i
                        class="fa fa-exclamation-triangle icon-right input-icon"
                        v-show="errors.has('name_en')"></i>
                      <label class="control-label" for="name_en">{{'team.name_en'
                        | translate}}</label><i class="bar"></i>
                      <small v-show="errors.has('name_en')"
                             class="help text-danger">{{
                        errors.first('name_en')
                        }}
                      </small>
                    </div>
                  </div>
                </div>
              </div>
              <div class="row row-reverse">
                <div class="col-9">
                  <div class="form-group with-icon-right"
                       :class="{'has-error': errors.has('position')}">
                    <div class="input-group">
                      <input
                        id="position"
                        name="position"
                        v-model="position"
                        v-validate="'required'"
                        required/>
                      <i
                        class="fa fa-exclamation-triangle icon-right input-icon"
                        v-show="errors.has('position')"></i>
                      <label class="control-label" for="position">{{'team.position'
                        | translate}}</label><i class="bar"></i>
                      <small v-show="errors.has('position')"
                             class="help text-danger">{{
                        errors.first('position')
                        }}
                      </small>
                    </div>
                  </div>
                </div>

                <div class="col-3">
                  <div class="form-group with-icon-right"
                       :class="{'has-error': errors.has('sort')}">
                    <div class="input-group">
                      <input
                        id="sort"
                        name="sort"
                        v-model="sort"
                        type="number"
                        v-validate="'required'"
                        required/>
                      <i
                        class="fa fa-exclamation-triangle icon-right input-icon"
                        v-show="errors.has('sort')"></i>
                      <label class="control-label" for="sort">{{'team.sort'
                        | translate}}</label><i class="bar"></i>
                      <small v-show="errors.has('sort')"
                             class="help text-danger">{{
                        errors.first('sort')
                        }}
                      </small>
                    </div>
                  </div>
                </div>
              </div>
            </div>

            <div class="col-4">
              <div class="row">
                <textarea
                  id="degrees"
                  name="degrees"
                  v-model="degrees"
                  class="col"
                  :palceholder="'team.degrees' | translate"
                  style="min-height: 150px;"
                ></textarea>
              </div>
            </div>

            <div class="col-4">
              <Uploader
                :maxFiles="1"
                :placeholder="'team.image' | translate"
                :images="getImage"
                :apiUrl="apiUrl"
                @onChange="reInitImage"
              />
            </div>
          </div>

          <div class="row">
            <div class="col col text-center mt-4">
              <button class="btn btn-success"  @click="update">
                {{'buttons.update' | translate}}
              </button>
            </div>
          </div>
        </vuestic-widget>        
      </div>
    </div>
  </div>
</template>

<script>
  import Vue from 'vue'
  import api from 'src/packages/Api'
  import Loading from 'partials/Loading'
  import Editor from 'view/components/editor'
  import Uploader from 'view/components/uploader'
  
  export default {
    name: 'update-team',
    data () {
      return {
        team: {},
        name: '',
        name_en: '',
        position: '',
        degrees: '',
        sort: 0,

        apiUrl: 'teams/' + this.$route.params.id + "/upload",
        loaded: false        
      }
    },
    components: {
      Loading,
      Uploader
    },
    created () {
      api.get(`teams/${this.$route.params.id}`).then(response => {
        const res = response.data

        this.team = res
        this.id = res.id
        this.name = res.name
        this.name_en = res.name_en
        this.position = res.position
        this.degrees = res.degrees
        this.sort = res.sort,

        this.loaded = true
      }).catch(error => {
        this.$store.dispatch('addToastMessage',
          { text: Vue.i18n.translate(`errors.${error.data.status_code}`) }
        )
        this.$router.push({ name: 'teams' })
      })
    },
    methods: {
      getImage () {
        const image = this.team.image
          ? [this.team.image.data] 
          : []

        return image
      },

      reInitImage (response) {
        this.team = response.data
      },

      update () {
        this.$validator.validateAll().then((result) => {
          if (result) {
            const data = {
              name: this.name,
              name_en: this.name_en,
              sort: this.sort,
              degrees: this.degrees,
              position: this.position    
            }

            api.put(`teams/${this.id}`, data).then(response => {
              this.$store.dispatch('addToastMessage',
                { text: Vue.i18n.translate('team.updated'), type: 'success' }
              )
              this.$router.push({ name: 'team', params: { id: response.data.id } })
            }).catch(error => {
              this.$store.dispatch('addToastMessage',
                { text: Vue.i18n.translate('errors.any'), error }
              )
            })
          }
        })
      }
    }
  }
</script>

